<template>
    <article>
      <section id="head">
        <div class="hdtop">
          <img :src="'/static/img/'+img" alt="">
          <h3> {{title}} </h3>
          <p>&nbsp;|&nbsp;{{time}}</p>
          <p>{{freight}}</p>
          <div id="hdright">
            <p class="collection">&#xe627;</p>
            <p class="shopInfo" > &#xe62d;</p>
          </div>
        </div>
          <div class="shop3-2">
            <i></i>
            <span>693</span>
          </div>
      </section>
      <section id="shopSearch">
        <div>
          <span>&#xe661;</span>
          <span></span>
        </div>
      </section>
       
        <div id="shop">
            <div class="left">
                <ul>
                    <li class="shopl" v-for="(ctg,index) in ctgs" :key="ctg.guid"> 
                        <p @click="showSecond(index)">{{ctg.text}}</p>
                        <ul>
                            <li class="shopl2" v-for="(children,index) in ctg.childrens" :key="children.guid" @click="shopl2(index)"> 
                                <a href="" @click.prevent="showGoods(children.text)"> {{children.text}} </a> 
                            </li>
                        </ul>
                    </li>
                </ul>
            </div>
            <div class="right">
                <div>
                    <h3> {{ctgText}} ({{count}}) </h3>
                </div>
                <ul>
                    <li v-for="gd in goods" :key="gd.guid" class="goodsid">
                      <img :src="gd.img" alt="">
                      <h5> {{gd.text}} </h5>
                      <p class="sales">{{gd.mon}} | {{gd.per}}%</p>
                      <span class="manjian"></span>
                      <p class="yen"> &yen; {{gd.price}} </p>
                      <div class="shopadd">
                        <!-- v-show="bus.carnum[gd.guid]" , -->
                          <span v-show="bus.carnum[gd.guid]">
                              <p class="sub" @click="subCar(gd)"> - </p>
                              <!--  -->
                              {{bus.carnum[gd.guid]}}
                          </span>
                          
                          <p class="add" @click="addCar(gd)"> + </p>
                      </div>
                  </li>
                </ul>
            </div>
        </div>
        <div id="shopnav" @click="nav()">&#xe670;
          <div class="shopmenu">
            <a href="#/">
              <span>&#xe66f;</span>
              <span></span>
            </a>
              <a href="#/classification">
                <span>&#xe634;</span>
                <span></span>
              </a>

            <a>
              <span>&#xe624;</span>
              <span></span>
            </a>
            <a href="">
              <span>&#xe607;</span>
              <span></span>
            </a>
            <a href="#/my">
              <span>&#xe772;</span>
              <span></span>
            </a>
          </div>
        </div>
        <div>
            <car :shopid="guid"></car>
        </div>
    </article>
  
</template>



<script>
//car
import car from "./car";
import Vue from "vue";
import Vant from "vant";
import "vant/lib/vant-css/index.css";

Vue.use(Vant);
const coupon = {
  available: 1,
  discount: 0,
  denominations: 150,
  origin_condition: 0,
  reason: "",
  value: 150,
  start_at: 1489104000,
  end_at: 1514592000
};
export default {
  props: ["guid", "title", "time", "img", "freight"],
  data() {
    return {
      ctgs: [],
      goods: [],
      ctgText: "",
      count: "",
      chosenCoupon: -1,
      coupons: [coupon]
    };
  },
  //car
  components: {
    car
  },
  created() {
    //ajax  //
    //
    //[this.guid]guid
    if (!this.bus.cars[this.guid]) {
      //guid
      this.$set(this.bus.cars, this.guid, []); //guid
    }
    //console.log(this.bus.cars);

    //ctgs: []
    this.jQuery.get(this.apiURL + "/ctgs", { guid: this.guid }, result => {
      if (result.errcode == 0) {
        //
        this.ctgs = result.data; //ctgs: [],
      }
    });
  },
  methods: {
    //goods
    showGoods(text, index) {
      this.jQuery.get(this.apiURL + "/goods", { ctgtext: text }, result => {
        if (result.errcode == 0) {
          //
          this.goods = result.data; //goods: [],
          this.ctgText = result.ctgText;
          this.count = result.count;
        }
      });
    },
    // +
    addCar(gd) {
      //
      var car = this.bus.cars[this.guid];
      //
      for (var i = 0; i < car.length; i++) {
        //guid,
        if (car[i].guid == gd.guid) {
          //
          if (this.bus.cars[this.guid][i].num < gd.store) {
            //guid,,,return
            this.bus.cars[this.guid][i].num++;
            //
            this.bus.cars[this.guid].splice(i, 1, this.bus.cars[this.guid][i]);
            //
            this.$set(
              this.bus.carnum,
              gd.guid,
              this.bus.cars[this.guid][i].num
            );
          }
          //console.log(this.bus.cars);
          return;
        }
      }
      //,
      gd.num = 1; //
      //
      if (gd.num >= gd.store) {
        //
        gd.num = gd.store;
      }
      this.bus.cars[this.guid].push(gd); //
      this.$set(this.bus.carnum, gd.guid, gd.num); //
    },
    // -
    subCar(gd) {
      //
      var car = this.bus.cars[this.guid];
      //
      for (var i = 0; i < car.length; i++) {
        //guid,
        if (car[i].guid == gd.guid) {
          //guid,
          var num = --this.bus.cars[this.guid][i].num;
          //
          this.bus.cars[this.guid].splice(i, 1, this.bus.cars[this.guid][i]);
          //0
          if (num <= 0) {
            delete this.bus.carnum[gd.guid]; //guid
            this.bus.cars[this.guid].splice(i, 1); //
          }

          this.$set(this.bus.carnum, gd.guid, num); //
          return;
        }
      }
    },
    //
    showSecond(index) {
      this.jQuery(".shopl ul")
        .eq(index)
        .slideToggle();
      this.jQuery(".shopl ul")
        .not(this.jQuery(".shopl ul").eq(index))
        .slideUp();
    },
    shopl2(index) {
      this.jQuery(".shopl2")
        .eq(index)
        .addClass("border")
        .siblings()
        .removeClass("border");
    },
    nav() {
      this.jQuery(".shopmenu").toggle();
    }
  }
};
</script>




<style>
* {
  font-family: "iconfont";
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
heml,
body {
  margin: 0;
  height: auto;
}
.clear {
  clear: both;
}
#head {
  background: url(../../static/img/55.jpg) 0 0 no-repeat;
  width: 100%;
  padding: 5px 10px;
  overflow: auto;
  position:fixed;
  top: 0;
  left: 0;
}
.hdtop {
  min-height: 62px;
  overflow: hidden;
}
.hdtop img {
  width: 52px;
  height: 53px;
  border-radius: 4px;
  border: 1px solid rgba(255, 255, 255, 0.8);
  background-color: #fff;
  display: inline-block;
  float: left;
  margin-top: 5px;
}
.hdtop h3 {
  line-height: 25px;
  font-size: 18px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  padding-top: 1px;
  margin-top: 5px;
  margin-left: 60px;
  color: #fff;
  margin-bottom: 0;
}
.hdtop p {
  line-height: 14px;
  font-size: 10px;
  margin: 0;
  margin-top: 2px;
  margin-left: 60px;
  color: #fff;
}
#hdright {
  float: right;
  margin-top: -50px;
}
#hdright .collection {
  float: right;
  font-size: 18px;
}
#hdright .shopInfo {
  width: 60px;
  height: 16px;
  top: 42px;
  padding-left: 5px;
  color: #fff;
  font-size: 10px;
  line-height: 14px;
  border-radius: 4px;
  background: rgba(70, 70, 70, 0.5);
  margin-top: 33px;
  padding-top: 3px;
}
#shopSearch {
  width: 100%;
  height: 40px;
  background: #eaeaea;
  overflow: hidden;
  position:fixed;
  top: 95px;
  left: 0;
}
#shopSearch > div {
  height: 28px;
  width: 95%;
  background: white;
  margin: 7px auto;
  line-height: 26px;
  padding-left: 10px;
}
#shopSearch div span {
  color: #999;
  font-size: 13px;
}
#shop {
  position: fixed;
  top: 136px;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  display: flex;
  padding-bottom: 165px;
}
#shop .left {
  float: left;
  width: 25%;
  overflow: auto;
}
#shop .right {
  float: left;
  width: 75%;
  overflow: auto;
  background:#fff;
}
#shop .shopl {
  border-bottom: 1px solid #eaeaea;
  border-right: 1px solid #eaeaea;
  background: #f8f8f8;
}
#shop p {
  margin: 0;
  height: 43px;
  line-height: 44px;
  font-size: 14px;
  padding: 0 5px;
  font-weight: 500;
  color: #666;
}
#shop .shopl2 {
  border-bottom: 1px solid #eaeaea;
  border-right: 1px solid #eaeaea;
  background: #fff;
}
#shop .shopl2 a {
  padding: 0 10px;
  color: #666;
  font-size: 13px;
  height: 43px;
  line-height: 44px;
}
#shop .shopl2 a:hover {
  text-decoration: none;
  color: #39ac69;
}
#shop .border {
  color: #47b34f;
  background: #fff;
  border-left: 3px solid #47b34f;
  border-right-color: #fff;
}
#shop .color {
  color: #47b34f;
}
#shop h3 {
  padding-left: 11px;
  color: #666666;
  font-size: 12px;
  line-height: 30px;
  background: #f8f8f8;
}
#shop .goodsid {
  overflow: hidden;
  padding: 10px 0 10px 10px;
  border-bottom: 1px solid rgb(229, 228, 228);
}
#shop .goodsid img {
  display: inline-block;
  float: left;
  padding-top: 4px;
  width: 65px;
  height: 65px;
  border-radius: 2px;
}
#shop .goodsid h5 {
  font-size: 14px;
  color: #333;
  overflow: hidden;
  padding-left: 5px;
  max-height: 42px;
  line-height: 21px;
  font-weight: 500;
  color: #333;
}
#shop .goodsid .sales {
  color: #999;
  display: inline-block;
  font-size: 11px;
  padding-left: 4px;
  margin-top: -20px;
  height: 12px;
  float: left;
}
#shop .goodsid .manjian {
  background: #5fbc65;
  color: #fff;
  border-radius: 2px;
  display: inline-block;
  padding: 1px 2px;
  line-height: 14px;
  font-size: 10px;
  margin-top: 15px;
  margin-left: 4px;
}
#shop .goodsid .yen {
  font-size: 16px;
  color: #ff3434;
  padding-top: 2px;
  white-space: nowrap;
  margin-top: -10px;
  margin-left: 64px;
}
#shop .goodsid .shopadd {
  float: right;
  margin-top: -35px;
  margin-right: 8px;
}
#shop .goodsid .add {
  width: 28px;
  height: 28px;
  border-radius: 50%;
  border: 1px solid #e5e5e5;
  text-align: center;
  line-height: 25px;
  color: #47b34f;
  font-size: 20px;
  display: inline-block;
}
#shop .goodsid .sub {
  width: 28px;
  height: 28px;
  border-radius: 50%;
  border: 1px solid #e5e5e5;
  text-align: center;
  line-height: 25px;
  color: #919391;
  font-size: 20px;
  display: inline-block;
}
#shopnav {
  position: fixed;
  bottom: 115px;
  z-index: 1000;
  right: 0;
  width: 36px;
  height: 36px;
  background: rgba(0, 0, 0, 0.8);
  border: 1px solid #757575;
  border-radius: 4px 0 0 4px;
  color: #fff;
  text-align: center;
  line-height: 35px;
}
#shopnav .shopmenu {
  position: absolute;
  right: -1px;
  bottom: 34px;
  width: 95px;
  border: 1px solid #757575;
  background: rgba(0, 0, 0, 0.8);
  display: none;
}
#shopnav .shopmenu a {
  display: block;
  padding-left: 38px;
  border-bottom: 1px solid #4b4b4b;
  line-height: 35px;
  height: 35px;
  color: #fff;
  font-weight: normal;
  padding: 0;
  text-align: left;
}
#shopnav .shopmenu a span {
  display: inline-block;
  margin-left: 10px;
  color: rgba(255, 255, 255, 0.897);
  font-size: 14px;
}
.shop3-2 {
  display: inline-block;
  width: 86%;
  font-size: 10px;
}
.shop3-2 i {
  background: #5FBC65;
  display: inline-block;
  padding: 0 2px;
  margin-right: 4px;
  border-radius: 2px;
  color: #ffffff;
  height: 14px;
  line-height: 14px;
  font-style: normal;
}
.shop3-2 span {
  display: inline-block;
  color: #ffffff;
  height: 14px;
  line-height: 14px;
  font-style: normal;
  text-overflow: ellipsis;
  white-space: nowrap;
}
</style>


