<template>
   <div id="regist">
       <header>
         <router-link to="/login" class="lh1"><span class="lh1"> &#xe604; </span></router-link>  
         <span class="lh2"></span>
       </header>
       <div id="regcont">
           <el-form id="regFrm" :rules="rules" label-position="left" ref="form"
                :model="form" label-width="80px" action="/">
                <el-form-item label="" prop="username">
                    <el-input v-model="form.username" name="username"></el-input>
                </el-form-item>

                <el-form-item label="" prop="password">
                    <el-input v-model="form.password" name="password" type="password"></el-input>
                </el-form-item>

                <el-form-item label="" prop="password2">
                    <el-input v-model="form.password2" type="password"></el-input>
                </el-form-item>

                <el-form-item label="" prop="phone">
                    <el-input v-model="form.phone" name="phone"></el-input>
                </el-form-item>

                <el-form-item label="" prop="email">
                    <el-input v-model="form.email" name="email"></el-input>
                </el-form-item>
                <el-form-item >
                   <el-button class="reg1" type="success"></el-button>
                   <el-button type="danger"></el-button>
                </el-form-item>
            </el-form>
       </div>
       
   </div>
</template>



<script>
export default {
  data() {
    var _this = this;
    //
    var chkPwd = function(value, callback) {
      if (value.trim() == "") {
        callback(new Error(""));
      } else if (value == _this.form.password) {
        callback();
      } else {
        callback(new Error(""));
      }
    };
    return {
      isShow: true,
      isCheckedAll: false,
      form: {
        username: "",
        password: "",
        password2: "",
        email: "",
        phone: "",
      },
      rules: {
        username: [
          {
            validator: function(rule, value, callback) {
              var usernamereg = /^[a-z]\w{2,17}$/;
              if (value.trim() == "") {
                callback(new Error(""));
              } else if (!usernamereg.test(value)) {
                callback(new Error("2-18"));
              } else {
                callback();
              }
            },
            trigger: "blur"
          }
        ],
        password: [
          {
            validator: function(rule, value, callback) {
              var len = value.length;
              if (value.trim() == "") {
                callback(new Error(""));
              } else if (len < 6 || len > 18) {
                callback(new Error("6-18"));
              } else {
                _this.$refs.form.validateField("password2");
                callback();
              }
            }
          }
        ],
        password2: [
          {
            validator: function(rule, value, callback) {
              chkPwd(value, callback);
            }
          }
        ],
        email: [
          {
            validator: function(rule, value, callback) {
              var emailreg = /^[a-z0-9]+([._\\-]*[a-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$/;
              if (value.trim() == "") {
                callback(new Error(""));
              } else if (!emailreg.test(value)) {
                callback(new Error(""));
              } else {
                callback();
              }
            },
            trigger: "blur"
          }
        ],
        phone: [
          {
            validator: function(rule, value, callback) {
              var phonereg = /^1([358][0-9]|4[579]|66|7[0135678]|9[89])[0-9]{8}$/;
              if (value.trim() == "") {
                callback(new Error(""));
              } else if (!phonereg.test(value)) {
                callback(new Error(""));
              } else {
                callback();
              }
            },
            trigger: "blur"
          }
        ]
      }
    };
  },
};
</script>


<style>
* {
  font-family: "iconfont";
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
html,
body {
  background: #fff;
}
#regist{
  background: #fff;
}
header {
  height: 45px;
  line-height: 45px;
  background: #f9f9f9;
  color: #616161;
  font-size: 17px;
  border-bottom: 1px solid #bbb;
  text-align: center;
}
header .lh1 {
  float: left;
  margin-left: 10px;
  color: #616161;
}
#regcont{
    padding:20px;
}
.reg1{
    margin-right:30px;
    margin-left: -20px;
}
</style>





