<template>
    <article id="shopcar">
        <div class="scont">
            <div class="stitle">
                <div>
                    40
                    <i>()</i>
                </div>
            </div>
            <div class="scarcon">

                <a href="" class="stable">-</a>
                <div class="sshop">
                    <ul class="simg">
                        <li class="slist">
                            <img src="/static/img/64.jpg" alt="">
                            <em>65.3</em>
                        </li>
                        <li class="slist">
                            <img src="/static/img/62.jpg" alt="">
                            <em>2.0</em>
                        </li>
                        <li class="slist">
                            <img src="/static/img/63.jpg" alt="">
                            <em>8.0</em>
                        </li>
                    </ul>

                </div>
            </div>
        </div>
        <footers></footers>
    </article>
</template>

<script>
import footers from "./footer.vue";
export default {
  components: {
    footers
  }
};
</script>

<style>
* {
  font-family: "iconfont";
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
html,
body {
  background: rgba(238, 237, 237, 0.372);
}
i {
  font-style: normal;
}
.scont {
  padding: 0 0 99px 0;
  text-align: center;
  background-color: #f4f4f4;
}
.stitle {
  display: inline-block;
  vertical-align: top;
  padding: 6px 7px;
  border-radius: 32px;
  background-color: #d1e7d3;
  margin: 15px 0 10px 0;
}
.stitle div {
  display: inline-block;
  vertical-align: top;
  line-height: 22px;
  padding: 0 14px 0 31px;
  border-radius: 22px;
  font-size: 14px;
  color: #fff;
  background: #47b34f url(/static/img/loc2.png) 13px center no-repeat;
  background-size: 10px 12px;
}
.scarcon {
  position: relative;
  margin: 0 10px 7px 10px;
  text-align: left;
  height: 141px;
  overflow: hidden;
  padding: 3px 5% 10px 2.6%;
  background: #fff;
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.1);
  border-radius: 2px;

}

.stable {
  display: block;
  z-index: 3;
  line-height: 44px;
  height: 147px;
  margin-bottom: -103px;
  color: #333;
  font-size: 15px;
  background: url(/static/img/m.png) right 17px no-repeat;
  background-size: 7px 11px;
}
.sshop {
  position: relative;
  z-index: 2;
  height: 92px;
  padding: 1px 0 0 0;
  border-top: 1px solid #ddd;
}
.simg {
  height: 97px;
  overflow: hidden;
  margin: 0;
  padding: 0;
}
.slist {
  width: 53px;
  height: 83px;
  display: inline-block;
  margin-top: 14px;
  text-align: center;
  font-size: 12px;
  line-height: 30px;
  color: #333;
  position: relative;
}
.slist img {
  vertical-align: top;
  width: 53px;
  height: 53px;
  border-radius: 5px;
}
.slist em {
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-style: normal;
  width: 53px;
}
</style>







