<script>
import "swiper/dist/css/swiper.min.css";
import Swiper from "Swiper";
import infiniteScroll from "vue-infinite-scroll"; //,
import footers from "./footer.vue";

export default {
  directives: { infiniteScroll: infiniteScroll }, //infiniteScroll
  data() {
    return {
      busy: false,
      shops: []
    };
  },
  methods: {
    loadMore() {
      //infiniteScroll
      this.busy = true;
      this.jQuery.get(this.apiURL + "/shops", result => {
        this.shops = this.shops.concat(result.data);
        this.busy = false;
      });
    }
  },
  created() {
    var $ = this.jQuery;
    $(window).scroll(function(event) {
      var top = $(this).scrollTop();
      var al = top / 106;
      if (al > 1) {
        return;
      }

      $(".search").css("background", "rgba(82,206,89, " + (1 - al) + ")");
      $(".topnav").css("background", "rgba(82,206,89, " + al + ")");

      if (top <= 10) {
        $(".topnav .tright").css({ marginRight: "-30px", transition: "1.5s" });
      } else {
        $(".topnav .tright").css({ marginRight: "0px", transition: "1.5s" });
      }
    });
    //shops
    this.jQuery.get(this.apiURL + "/shops", result => {
      this.shops = result.data;
    });
  },
  mounted() {
    var mySwiper = new Swiper(".swiper-container", {
      autoplay: {
        delay: 2000, //
        stopOnLastSlide: false,
        disableOnInteraction: false
      },
      loop: true, //
      speed: 2000, //
      pagination: {
        //
        el: ".swiper-pagination",
        clickable: true
      }
    });
  },
  components: {
    footers
  }
};
</script>





