<template>
  <div id="car">
    <div class="iconcar" @click="iconshow">
      <span>&#xe624;</span>
      <div v-show="totalNum>0">{{totalNum}}</div>
    </div>
    <table>
      <p class="empty" @click="emptyCar"> <van-icon name="delete" />  </p>
      <van-checkbox class="chk" v-model="checkeds" ></van-checkbox>

      <tr v-for="car in bus.cars[shopid]" :key='car.guid'>
          <td>
            <input type="checkbox" v-model="checked" :value="car.guid">
          </td>
          <img :src="car.img" alt="">
          <td class="cartext"> {{car.text}} </td>
          <td class="carprice">&yen; {{car.price}}</td>

          <td class="choose">
              <p class="sub" @click="subCar(car)"> - </p>
              {{car.num}}
              <p class="add" @click="addCar(car)"> + </p>
          </td>
          <!-- <td> &yen; {{car.price*car.num}}</td> -->
      </tr>
    </table>
    <div id="carfot">
      <div class="total"> &yen; <b> {{total}} </b></div>
      <div class="pay" v-show="totalNum>0">
        
      </div>
    </div>
   
    
  </div>
</template>



<script>
import Vue from "vue";
import Vant from "vant";
import "vant/lib/vant-css/index.css";

Vue.use(Vant);

export default {
  //shopid
  props: ["shopid"],
  data() {
    return {
      checked: [],
      checkeds: false
    };
  },
  computed: {
    //
    total() {
      var sum = 0;
      for (var i = 0; i < this.bus.cars[this.shopid].length; i++) {
        var gid = this.bus.cars[this.shopid][i].guid;
        //gid,continue
        if (this.checked.indexOf(gid) == -1) {
          continue;
        }
        sum +=
          this.bus.cars[this.shopid][i].price *
          this.bus.cars[this.shopid][i].num;
      }
      return sum;
    },
    //
    totalNum() {
      var sum = 0;
      var shopid = this.bus.cars[this.shopid];
      for (var i = 0; i < shopid.length; i++) {
        sum += shopid[i].num;
      }
      return sum;
    }
  },
  methods: {
    //
    emptyCar() {
      //carsthis.shopid
      this.$set(this.bus.cars, this.shopid, []);
      //buscarnum
      this.$set(this.bus, "carnum", {});
    },

    //
    addCar(gd) {
      //
      var car = this.bus.cars[this.shopid];
      //
      for (var i = 0; i < car.length; i++) {
        //guid,
        if (car[i].guid == gd.guid) {
          //
          if (this.bus.cars[this.shopid][i].num < gd.store) {
            //guid,,,return
            this.bus.cars[this.shopid][i].num++;
            //
            this.bus.cars[this.shopid].splice(
              i,
              1,
              this.bus.cars[this.shopid][i]
            );
            //
            this.$set(
              this.bus.carnum,
              gd.guid,
              this.bus.cars[this.shopid][i].num
            );
          }
          //console.log(this.bus.cars);
          return;
        }
      }
      //,
      gd.num = 1; //
      //
      if (gd.num >= gd.store) {
        //
        gd.num = gd.store;
      }
      this.bus.cars[this.shopid].push(gd); //
      this.$set(this.bus.carnum, gd.guid, gd.num); //
    },
    // -
    subCar(gd) {
      //
      var car = this.bus.cars[this.shopid];
      //
      for (var i = 0; i < car.length; i++) {
        //guid,
        if (car[i].guid == gd.guid) {
          //guid,
          var num = --this.bus.cars[this.shopid][i].num;
          //
          this.bus.cars[this.shopid].splice(
            i,
            1,
            this.bus.cars[this.shopid][i]
          );
          //0
          if (num <= 0) {
            delete this.bus.carnum[gd.guid]; //guid
            this.bus.cars[this.shopid].splice(i, 1); //
          }

          this.$set(this.bus.carnum, gd.guid, num); //
          return;
        }
      }
    },
    //
    iconshow() {
      if (this.bus.cars[this.shopid].length > 0) {
        this.jQuery("table").slideToggle();
        this.jQuery(".iconcar").css({ top: "-20px" });
      }
    }
  }
};
</script>




<style>
* {
  font-family: "iconfont";
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
#car {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
}
table {
  width: 100%;
  border-collapse: collapse;
  display: none;
  overflow: hidden;
  background: #fff;
  border-top: 1px solid #dddddd;
}
.clear {
  clear: both;
  margin: 0;
}

#car .iconcar {
  height: 48px;
  width: 48px;
  border-radius: 50%;
  background: #bebebe;
  line-height: 48px;
  text-align: center;
  margin-top: 2px;
  color: white;
  font-size: 20px;
  float: left;
  background: #47b34f;
  margin-left: 10px;
}
#car .iconcar div {
  height: 17px;
  width: 17px;
  border-radius: 50%;
  background: #ff5d5c;
  color: white;
  font-size: 12px;
  text-align: center;
  line-height: 17px;
  float: left;
  margin-left: 35px;
  margin-top: -45px;
}
.empty {
  padding: 0 12px 0 18px;
  color: #666;
  float: right;
  margin: 0;
  font-size: 15px;
  padding: 10px;
}
.chk {
  color: #333;
  padding-left: 20px;
  padding-right: 20px;
  padding: 10px;
}
table img {
  height: 55px;
  width: 55px;
  margin-top: 10px;
  float: left;
}
.add {
  width: 28px;
  height: 28px;
  border-radius: 50%;
  border: 1px solid #e5e5e5;
  text-align: center;
  line-height: 25px;
  color: #47b34f;
  font-size: 20px;
  display: inline-block;
}
.sub {
  width: 28px;
  border-radius: 50%;
  border: 1px solid #e5e5e5;
  text-align: center;
  line-height: 25px;
  color: #919391;
  font-size: 20px;
  display: inline-block;
}
#carfot .total {
  width: 150px;
  float: left;
  margin-top: 10px;
  font-size: 20px;
  color: red;
  padding-left: 20px;

}
#carfot .pay {
  height: 50px;
  width: 120px;
  color: white;
  font-size: 16px;
  background: #47b34f;
  line-height: 50px;
  text-align: center;
  float: right;
}
#carfot {
  border-top: 1px solid #dddddd;
  width: 100%;
  height: 50px;
  background: #fff;
}

#car input[type="checkbox"] {
  -webkit-appearance: none;
  background: #fff url("/static/img/ck.png") center top;
  background-size: 100%;
  height: 20px;
  vertical-align: middle;
  width: 20px;
  outline: none;
  margin: 0 10px;
  margin-top: 5px;
}
#car input[type="checkbox"]:checked {
  background-position: 0 -200px;
  outline: none;
}
.cartext {
  min-height: 36px;
  line-height: 36px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  color: #555;
  float: left;
  margin-left: 10px;
}
.carprice {
  line-height: 20px;
  color: #ff3434;
  font-size: 16px;
  float: left;
  margin-top: 38px;
  margin-left: -63px;
}
.choose{
  float: right;
  margin-right: -190px;
  margin-top: 20px;
}
.choose P{
  margin:5px;
}
</style>


