<template>
    <article id="login">
       <header>
         <router-link to="/my" class="lh1"><span class="lh1"> &#xe604; </span> </router-link>
         <span class="lh2"></span>
       </header>
       <div id="ltab">
         <p @click="ltab1">
           <span class="ltab1s red"></span>
         </p>
         <p @click="ltab2">
          <span class="ltab2s"></span>
         </p>
       </div>
       <section id="locont">
        <div v-show="isShow">
          <van-cell-group>
            <van-field class="lborder" 
              label=""
              icon="clear"
              placeholder="//"
              @click-icon="username = ''"
            />
            <van-field class="lborder"
              type="password"
              label=""
              placeholder=""
            />
          </van-cell-group>
        </div>
        <div v-show="!isShow">
          <van-cell-group>
             <van-field class="lborder"
              label=""
              placeholder=""
            />
            <van-field class="lborder"
              center
              label=""
              placeholder=""
              icon="clear"
              @click-icon="sms = ''"
            >
              <van-button slot="button" size="small" type="primary"></van-button>
            </van-field>
          </van-cell-group>
        </div>
        <a href="" class="login"> </a>
        <a href="" class="login2"></a>
        <div class="login3">
          <a href="">&#xe61b; </a>
          <a href="#/regist">&#xe617; </a>
        </div>
      </section>
      <div id="lotooter">
        <h4></h4>
        <a href="">&#xe61e;</a>
        <p> <a href=""></a></p>
      </div>
        
      
    </article>
</template>



<script>
import Vue from "vue";
import Vant from "vant";
import "vant/lib/vant-css/index.css";

Vue.use(Vant);


export default {
  data() {
    return {
      isShow: true,
    };
  },
  methods: {
    //
    ltab1() {
      this.jQuery(".ltab1s").addClass("red");
      this.jQuery(".ltab2s").removeClass("red");
      this.isShow = true;
    },
    ltab2() {
      this.jQuery(".ltab2s").addClass("red");
      this.jQuery(".ltab1s").removeClass("red");
      this.isShow = false;
    },
  }
};
</script>


<style>
* {
  font-family: "iconfont";
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
html,
body {
  background: #fff;
}
#login{
  background: #fff;
}
header {
  height: 45px;
  line-height: 45px;
  background: #f9f9f9;
  color: #616161;
  font-size: 17px;
  border-bottom: 1px solid #bbb;
  text-align: center;
}
header .lh1 {
  float: left;
  margin-left: 10px;
  color: #616161;
}
#ltab {
  line-height: 44px;
  font-size: 14px;
  border-bottom: 1px solid #d7d7d7;
  overflow: hidden;
}
#ltab p {
  width: 50%;
  float: left;
  text-align: center;
  margin: 0;
}
#ltab p span {
  padding: 13px 10px;
}
.red {
  border-bottom: 2px solid #f23030;
}
#locont {
  padding: 0 20px 20px 20px;
}
.lborder {
  border-bottom: 1px solid #d7d7d7;
  height: 48px;
}
.login {
  display: block;
  height: 45px;
  line-height: 46px;
  background-color: #eee;
  border-radius: 2px;
  margin-top: 30px;
  font-size: 14px;
  text-align: center;
  color: #848689;
}
.login2 {
  display: block;
  color: #f23030;
  border: 1px solid #f23030;
  margin-top: 10px;
  height: 45px;
  line-height: 46px;
  font-size: 14px;
  text-align: center;
}
.login3 {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}
.login3 a {
  color: #333;
  font-size: 13px;
}
#lotooter {
  margin-top: 85px;
  padding:20px;
  border-top: 1px solid #d7d7d7;
  text-align: center;
  overflow: hidden;
}
#lotooter h4{
  color: #bfbfbf;
}
#lotooter a{
  display: inline-block;
  font-size: 22px;
  padding-top:20px;
  padding-bottom: 10px;
}
#lotooter p{
  font-size: 14px;
}
#lotooter p a{
  font-size: 14px;
  text-decoration: underline;
}
</style>





