<template>

  <article>
      <div class="classsearch">
            <span>&#xe661;</span>
            <span></span>
        </div>
        <div class="classify">
            <div class="classleft">
                <ul>
                    <li class="kind" v-for="(list,index) in lists" :key="list.guid" @click="showGoods(list.text,index)">
                        <router-link class="route" to="">{{list.text}}</router-link> 
                    </li>
                </ul>
            </div>
            <div class="classright">
                <ul>
                    <li v-for="cls in clss" :key="cls.guid">
                        <p class="classtitle">{{cls.title}}</p>
                        <ul class="dtls">
                            <li  class="dtl" v-for="children in cls.childrens" :key="children.guid">
                                <div><img :src="children.img" alt=""></div>
                                <p>{{children.text}}</p>
                            </li>
                        </ul>
                    </li>
                </ul>
            </div>
        </div>
    <footers></footers>
  </article>

</template>



<script>
import footers from "./footer.vue";
export default {
  data() {
    return {
      clss: [],
      lists: []
    };
  },
  created() {
    this.jQuery.get(this.apiURL + "/lists", { guid: this.guid }, result => {
      if (result.errcode == 0) {
        this.lists = result.data;
      }
    });
  },
  components: {
    footers
  },
  methods: {
    showGoods(text, index) {
      this.jQuery.get(this.apiURL + "/category", { clstext: text }, result => {
        if (result.errcode == 0) {
          this.clss = result.data;
        }
      });
      this.jQuery(".kind").eq(index).addClass("border").siblings().removeClass("border");
    }
  },


};
</script>
<style>
* {
  font-family: "iconfont";
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
heml,
body {
  height: 2000px;
}
ul {
  list-style: none;
}
.classsearch {
  margin-top: 10px;
  border: 1px solid #e6e6e6;
  width: 92%;
  height: 30px;
  display: flex;
  overflow: hidden;
  position: fixed;
  top: 0;
  left: 4%;
}
.classsearch span:nth-child(1) {
  display: block;
  line-height: 30px;
  color: #666;
  margin-left: 5px;
  font-size: 12px;
}
.classsearch span:nth-child(2) {
  display: block;
  line-height: 30px;
  margin-left: 10px;
  color: #666;
  font-size: 12px;
}
.classify {
  display: flex;
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: fixed;
  top: 47px;
  border-top: 1px solid #f8f8f8;
}
.classleft {
  width: 25%;
  background: #f8f8f8;
}
.classright {
  width: 75%;
  overflow: auto;
  margin-bottom: 80px;
  background: #fff;
}
.classleft .kind {
  width: 100%;
  height: 43px;
  text-align: center;
  line-height: 44px;
  padding: 0 11px;
  font-size: 14px;
  border-bottom: 1px solid #eaeaea;
}
.border {
  padding-left: 11px;
  color: #47b34f;
  background: #fff;
  border-left: 3px solid #47b34f;
  border-right-color: #fff;
}

.classtitle {
  font-size: 14px;
  padding-left: 10px;
  color: #7d7d7d;
  margin-top: 10px;
  margin-left: 5px;
}
.classright .dtls {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  width: 100%;
}
.classright .dtl {
  width: 30%;
  margin-left: 7px;
}
.dtl div {
  height: 70px;
  width: 70px;
  margin: 0 auto;
}
.dtl div img {
  height: 100%;
  width: 100%;
}
.dtl p {
  text-align: center;
  font-size: 12px;
  color: #7d7d7d;
}
.route:hover {
  text-decoration: none;
  color: #39ac69;
}
.route {
  color: #666;
}
</style>





