<template>
  <article id="order">
    <div class="ocont">
      <p class="otop">
          <span class="otop1">&#xe72e;</span>
          <span class="otop2"></span>
      </p>
      <p class="otime">04-08 20:11</p>
      <div class="details">
          <img src="/static/img/58.jpg" alt="">
          <img src="/static/img/64.jpg" alt="">
          <div class="detright">
              <span class="oprice">65.3</span>
              <span class="onum"></span>
              <span class="son"></span>
          </div>
      </div>
      <div class="obuy">
          <a href=""></a>
      </div>
    </div>

    <p class="ofoot"></p>
    <footers></footers>
  </article>
</template>

<script>
import footers from "./footer.vue";
export default {
   components:{
    footers
  }
};
</script>

<style>
* {
  font-family: "iconfont";
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
html,
body {
  background: rgba(238, 237, 237, 0.372);
}
#order{
  background: rgba(238, 237, 237, 0.372);
}
.ocont {
  background: #fff;
  color: #333;
  padding: 0 10px 15px;
  overflow: hidden;
}
.otop {
  height: 34px;
  font-size: 15px;
  background-size: 44px auto;
  white-space: nowrap;
  color: #333;
  margin-bottom: 0px;
}
.otop1 {
  display: inline-block;
  max-width: 90%;
  height: 40px;
  line-height: 40px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  font-weight: bold;
}
.otop2 {
  color: #333333;
  height: 40px;
  line-height: 40px;
  font-size: 13px;
  float: right;
}
.otime {
  line-height: 1;
  color: #999;
  margin: 0;
  font-size: 12px;
}
.details {
  margin-top: 10px;
  position: relative;
}
.details img {
  display: inline-block;
  vertical-align: middle;
  margin: 5px 0 5px 0.1rem;
  width: 55px;
  height: 55px;
}
.detright {
  width: 65px;
  height: 55px;
  position: absolute;
  right: 0;
  top: 5px;
}
.oprice {
  font-size: 15px;
  color: #ff5757;
  height: 19px;
  padding-left: 10px;
  float: right;
}
.onum {
  font-size: 13px;
  color: #666;
  padding-bottom: 3px;
  float: right;
}
.son {
  display: inline-block;
  border: 1px solid #16a9ff;
  font-size: 0;
  border-radius: 2px;
  color: #16a9ff;
  height: 15px;
  overflow: hidden;
  padding: 2px;
  font-size: 10px;
  line-height: 1;
  float: right;
}
.obuy {
  padding-top: 15px;
  float: right;
}
.obuy a {
  margin-left: 0;
  color: #333;
  border-color: #d9d9d9;
  font-size: 14px;
  display: inline-block;
  margin-left: 10px;
  padding: 6px 8px;
  color: #333;
  border: 1px solid #d9d9d9;
  border-radius: 2px;
  text-align: center;
}
.ofoot {
  line-height: 40px;
  padding: 0;
  text-align: center;
  margin-top: 10px;
  background: #fff;
  color: #3c3c3c;
}
</style>



